import React from 'react'
import style from "../../assets/scss/index.module.scss";
import classNames from "classnames";

export const blockquote = (props: any) => {
    const {children} = props;

    function multiBlockQuote() {

        return children.map(({props}: any, index: number) => {

            if (typeof props.children === 'string' || Array.isArray(props.children)) {
                return <p className={style.gMb0} key={index}>
                    {props.children}
                </p>
            }

            if (props.children.props.originalType === "em" ||
                props.children.props.originalType === "a") {
                return <p className={classNames(style.gMb0, style.floatRight)} key={index}>
                    {props.children}
                </p>
            }

            return <h3 className={classNames(style.h3, style.gFontWeight300, style.gMb20)}>
                {props.children}
            </h3>
        });
    }

    return <div
        className={classNames(style.gBrdBlueLeft, style.uShadowV5, style.gBgWhite, style.gBrdAround, style.gBrdGrayLightV4, style.gLineHeight2, style.gPa40, style.gMb30)}>
        {
            Array.isArray(children) ?
                multiBlockQuote() :
                <p className={style.gMb0}>
                    {children.props.children}
                </p>
        }
    </div>
}

