import React from 'react'
import style from "../../assets/scss/index.module.scss";
import classNames from "classnames";

export const link = (props: any) => {
    const { href,
        children
    } = props;
    return <a
        className={classNames(style.dBlock, style.uLinkV5, style.gColorGrayDarkV4, style.rounded, style.gPy8, style.gColorPurpleHover)}
        href={href} target="_blank">
        {children}
    </a>
}