import React from 'react'
import style from "../../assets/scss/index.module.scss";
import classNames from "classnames";
import {graphql, useStaticQuery} from "gatsby";
import Img from "gatsby-image";

export const img = React.memo<any>((props: any) => {
    const data = useStaticQuery(graphql`
        query {
            allImageSharp {
                edges {
                    node {
                        fluid(maxWidth: 900) {
                            ...GatsbyImageSharpFluid
                            originalName
                        }
                    }
                }
            }
        }
    `)

    const imageName = props.src.substring(2);
    const image = data.allImageSharp.edges.filter((x: any) => x.node.fluid.originalName == imageName)[0];

    return <div className={classNames(style.textCenter, style.gMb30)}>
        <Img className={classNames(style.imgFluid, style.uShadowV2, style.gRounded3, style.gMb10)}
            fluid={image.node.fluid}
            fadeIn={true}
            durationFadeIn={500}
            alt={props.alt}/>
            <em className={classNames(style.gColorGrayLightV1)}>{props.alt}</em>
    </div>
});